@import "../layout/variable.scss";
.post_com {
  height: calc(100vh - 50px);
  overflow: auto;
  padding-bottom: 50px;
  .top_comments_post {
    padding: 15px 0 10px 25px;
    border-bottom: 4px solid #ebeef2;
    .post_body {
      .comment_header {
        .post_byline {
          .post_username {
            margin-bottom: 10px;
            a {
              color: #353a3d;
              font-weight: 700;
              text-decoration: none;
              font-size: 22px;
              line-height: 1.25em;
              word-wrap: break-word;
            }
          }
        }
      }
      .comment_body {
        display: flex;
        margin-bottom: 10px;
        .post_comment {
          p {
            font-size: 15px;
          }
        }
      }
      .comment_footer {
        display: flex;
        .post_like {
          border-right: 2px solid #c2c6cc;
          padding-right: 5px;
        }
        .post_dislike {
          margin-left: 10px;
        }
        .post_like,
        .post_dislike {
          a {
            font-size: 11px;
            color: $dark-text;
            margin-right: 8px;
            span {
              padding-right: 3px;
            }
          }
        }
        .reply_footer {
          .bullet {
            width: 4px;
            height: 4px;
            border-radius: 50%;
            background-color: #c2c6cc;
            display: inline-block;
            margin: 3px 7px;
          }
        }
      }
    }
  }
}
