@import "../../layout/variable";
.CommentcompStyles {
  .main__container {
    max-width: 100%;
    .avtar_massemmo {
      position: relative;
      .avatar_massage {
        padding: 20px 0;
        padding-bottom: 10px;
        display: flex;
        align-items: flex-start;
        .massage_textarea {
          max-width: 100%;
          width: 100%;
          position: relative;
          height: 150px;
          border: 2px solid #dbdfe4;
          border-radius: 4px;
          @media #{$IpadP} {
            max-width: 100%;
          }
          .mass_text {
            width: 100%;
            height: calc(100% - 37px);
            color: #2a2e2e;
            padding: 10px 15px;
            font-size: 16px;
            font-weight: 400;
            position: absolute;
            border: 0px;
            left: 0;
            top: 0;
            resize: none;
            &:focus {
              outline: none;
            }
          }
          .commentemoji_btn {
            position: absolute;
            background-color: #f6f8f9;
            border-radius: 4px;
            bottom: 0px;
            left: 0px;
            padding: 5px 10px;
            display: flex;
            width: 100%;
            align-items: center;
            justify-content: space-between;
            .emojiundo_btn {
              display: flex;
              align-items: center;
              .emo_button {
                border: 1px solid transparent;
                background-color: transparent;
                cursor: pointer;
                margin: 0 2px;
                width: 28px;
                height: 24px;
                display: flex;
                justify-content: center;
                align-items: center;
                transition: 0.3s ease;
                &:focus {
                  outline: none;
                }
                &:hover {
                  border-color: #c2c6cc;
                  border-radius: 3px;
                  background-color: #fff;
                }
              }
            }
            .undo {
              background-image: url("http://demo-dexos.simform.solutions/static/front/media/redo.111c3216.svg");
              background-size: contain;
              background-repeat: no-repeat;
              background-position: center;
              visibility: visible;
            }
            .redo {
              background-image: url("http://demo-dexos.simform.solutions/static/front/media/undo.413f6136.svg");
              background-size: contain;
              background-repeat: no-repeat;
              background-position: center;
              visibility: visible;
            }
            .post_comment {
              .proceed_button {
                background: rgba(29, 47, 58, 0.6);
                padding: 3px 20px;
                border-radius: 3px;
                color: #fff;
                font-size: 18px;
                font-weight: 500;
                border: none;
                cursor: pointer;
                &:focus {
                  outline: none;
                }
              }
            }
          }
        }
      }
      aside.emoji-picker-react {
        position: absolute;
        top: 50%;
        left: 2px;
        z-index: 99;
        height: 200px;
      }
      .loginwith_singup {
        margin-bottom: 15px;

        // .social_icon {
        //   display: flex;
        //   flex-direction: column;
        //   align-items: flex-start;
        //   margin: 0 10px 0 0;
        //   .login_content {
        //     font-size: 15px;
        //     line-height: 18px;
        //     font-weight: 700;
        //     color: $dark-text;
        //     text-transform: uppercase;
        //     margin-bottom: 10px;
        //   }
        //   .social_btn {
        //     display: flex;
        //     padding: 0;
        //     margin: 0;
        //     li {
        //       list-style: none;
        //       margin: 0 10px 0 0;
        //       .social {
        //         border: none;
        //         padding: 0;
        //         font-size: 25px;
        //         border-radius: 50%;
        //         overflow: hidden;
        //         background: transparent;
        //         cursor: pointer;
        //         &:focus {
        //           outline: none;
        //         }
        //         span {
        //           border-radius: 50%;
        //           font-size: 35px;
        //         }
        //       }
        //     }
        //   }
        // }
        .username_emai {
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          width: 50%;
          margin-bottom: 30px;
          @media #{$IpadP} {
            width: 75%;
            margin-bottom: 10px;
          }
          @media #{$PhoneS} {
            width: 100%;
          }
          .singup_title {
            font-size: 15px;
            line-height: 18px;
            font-weight: 700;
            color: $dark-text;
            text-transform: uppercase;
            margin-bottom: 10px;
          }
          .formitem_name {
            max-width: 100%;
            width: 100%;
            display: flex;
            flex-direction: column;
            .ant-form-item {
              margin: 0;
              input {
                margin: 5px 0;
                padding: 10px 10px;
                max-width: 100%;
                width: 100%;
                border: 1px solid #dbdfe4;
                border-radius: 4px;
                background: #fff;
                font-size: 13px;
                &:focus {
                  outline: none;
                  box-shadow: 0 0 12px 0 rgba(0, 0, 0, 0.095);
                }
              }
            }
          }
          .privacy_policy {
            margin: 0;
            @media #{$PhoneS} {
              display: none;
            }
            .privacy_ele {
              font-size: 14px;
              color: $dark-text;
              font-weight: 400;
              line-height: 18px;
              margin: 0 0 10px 0;
              a {
                text-decoration: none;
                color: $link-color;
                margin: 0 5px;
              }
            }
          }
          .guest_checkbox {
            margin: 0;
            font-size: 14px;
            color: $dark-text;
            font-weight: 400;
            line-height: 18px;
            margin-bottom: 15px;
            @media #{$PhoneS} {
              text-align: left;
            }
            input {
              cursor: pointer;
            }
          }
        }
      }
    }
    .viewallcomment_btn {
      display: flex;
      justify-content: center;
      margin: 0 0 40px 0;
      @media #{$IpadP} {
        justify-content: flex-start;
        width: 100%;
      }
      .ViewComments_btn {
        padding: 6px 18px;
        border-radius: 4px;
        border: none;
        background-color: $link-color;
        color: #f7f7f7;
        font-size: 17px;
        cursor: pointer;
        height: auto;
        &:focus {
          outline: none;
        }
      }
    }
    .all_post {
      .post_list {
        margin: 0;
        padding: 0;
        .post {
          list-style: none;
          margin: 20px 0 20px 0;
          .post_body {
            display: flex;
            flex-direction: column;
            width: calc(100% - 50px);
            .comment_header {
              display: flex;
              align-items: center;
              margin-bottom: 8px;
              @media #{$PhoneS} {
                flex-wrap: wrap;
              }
              .post_byline {
                display: flex;
                align-items: center;
                .post_username {
                  margin: 0 5px 0 0;
                  a {
                    text-decoration: none;
                    font-size: 18px;
                    font-weight: bold;
                    line-height: 22px;
                    &:hover {
                      text-decoration: underline;
                    }
                    @media #{$PhoneS} {
                      font-size: 16px;
                    }
                  }
                }

                .badge {
                  width: 30px;
                  height: 13px;
                  color: #fff;
                  background: #687a86;
                  font-size: 10px;
                  font-weight: 700;
                  border-radius: 3px;
                  display: inline-block;
                  display: none;
                }
              }
              .bullet {
                width: 4px;
                height: 4px;
                border-radius: 50%;
                background-color: #c2c6cc;
                display: inline-block;
                margin: 3px 7px;
              }
              .post_user {
                margin-right: 8px;
                color: $post-user-color;
                font-size: 15px;
                font-weight: 600;
                .postuser_arrow {
                  background-image: url("http://demo-dexos.simform.solutions/static/front/media/redo.111c3216.svg");
                  background-size: contain;
                  background-repeat: no-repeat;
                  background-position: center;
                  visibility: visible;
                  background-color: transparent;
                  border: none;
                  box-shadow: none;
                  max-width: 15px;
                  width: 100%;
                  height: 14px;
                  padding: 0px 20px 4px 5px;
                }
              }
              .post_meta {
                font-size: 12px;
              }
            }
            .comment_body {
              display: flex;
              margin-bottom: 10px;
            }
            .comment_footer {
              display: flex;
              align-items: center;
              @media #{$PhoneS} {
                flex-wrap: wrap;
              }
              .post_like {
                border-right: 2px solid #c2c6cc;
                padding-right: 8px;
              }
              .post_dislike {
                margin-left: 8px;
              }
              .post_like,
              .post_dislike {
                a {
                  font-size: 11px;
                  color: $dark-text;
                  margin-right: 8px;
                  span {
                    padding-right: 3px;
                  }
                }
                .anticon-dislike,
                .anticon-like {
                  margin-right: 7px;
                }
              }
              .reply_footer {
                .reply_text {
                  cursor: pointer;
                }
                .bullet {
                  width: 4px;
                  height: 4px;
                  border-radius: 50%;
                  background-color: #c2c6cc;
                  display: inline-block;
                  margin: 3px 7px;
                }
              }
              .share_footer {
                display: flex;
                align-items: center;
                transition: 0.3s ease;
                &:hover .share_hover {
                  visibility: visible;
                }
                .share_hover {
                  margin-left: 20px;
                  transition: 0.3s ease;
                  visibility: hidden;
                  .sharehover_icon {
                    margin: 0 4px;
                    border: 0;
                    background: transparent;
                    box-shadow: none;
                    color: $link-color;
                    border-radius: 3px;
                    cursor: pointer;
                    padding: 0;
                    width: 22px;
                    height: 22px;
                    &:focus {
                      outline: none;
                    }
                    &:hover {
                      opacity: 0.9;
                    }
                  }
                }
              }
            }
          }
        }
        .ci_post {
          margin: 10px 10px 20px 30px;
        }
        .ci_post1 {
          margin: 10px 10px 20px 30px;
        }
      }
    }
    .comment_nav {
      border-bottom: 2px solid #e7e9ee;
      margin-bottom: 10px;
      ul {
        display: flex;
        padding: 0;
        margin: 0;
        @media #{$PhoneS} {
          flex-wrap: wrap;
        }
        li {
          list-style: none;
          margin-right: 63px;
          @media #{$PhoneS} {
            margin-right: 30px;
          }
          &.active {
            .ant-btn-link {
              position: relative;
              &::after {
                content: " ";
                display: block;
                height: 2px;
                background: $link-color;
                position: absolute;
                bottom: -2px;
                left: 0;
                right: 0;
              }
            }
          }
          &:last-child {
            margin-left: auto;
            margin-right: 0;
            @media #{$PhoneS} {
              margin-left: 0;
              display: none;
            }
          }
        }
      }
      .ant-btn-link {
        color: $link-color;
        background-color: transparent;
        border: none;
        cursor: pointer;
        text-transform: capitalize;
        font-size: 15px;
        line-height: 3;
        font-weight: bold;
        padding: 0;
        height: auto;
        &:focus {
          outline: 0;
        }
      }
    }
  }
  .ant-form-item {
    width: 100%;
  }
}
.post-main {
  height: calc(100vh - 50px);
  overflow: auto;
  padding-bottom: 50px;
  .post-list {
    display: flex;
    padding: 20px;
    border-bottom: 4px solid #ebeef2;
    &__info {
      flex: 1 1 auto;
      padding-right: 20px;
      .post-time {
        font-size: 16px;
        color: $dark-text;
        margin-bottom: 10px;
      }
      h2 {
        font-size: 22px;
        line-height: 1.25em;
        font-weight: 600;
        @media #{$PhoneS} {
          font-size: 17px;
        }
      }
      a {
        font-size: 15px;
      }
    }
    &__image {
      flex: 1 1 150px;
      @media #{$PhoneS} {
        flex: 100%;
      }
      img {
        @media #{$PhoneS} {
          width: 100%;
        }
      }
    }
  }
}

.jointhe {
  width: 100%;
  height: 35px;
  float: left;
  margin-bottom: 30px;
  @media #{$IpadP} {
    width: 100%;
    margin-bottom: 20px;
  }
}
.FootercompStyles {
  .footer_bottom {
    padding: 20px;
    border-radius: 30px;
    padding-top: 30px;
    .footer_content {
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-top: 2px solid #b9bec3;
      padding-top: 4px;
      .footer_ele {
        display: flex;
        align-items: center;
        .footer_text {
          display: flex;
          margin: 0 20px;
          font-size: 18px;
          color: #353a3d;
          a {
            text-decoration: none;
            font-size: 18px;
            color: #353a3d;
          }
          &:first-child {
            margin-left: 0;
          }
          .footersub_icon {
            margin-right: 8px;
          }
        }
      }
      .footer_logo {
        font-size: 26px;
        color: #687a86;
        font-weight: 800;
      }
    }
  }
}
.ant-drawer-right.ant-drawer-open {
  .ant-drawer-content-wrapper {
    @media #{$PhoneL} {
      width: 520px !important;
    }
    @media #{$PhoneS} {
      width: 320px !important;
    }
  }
}
.cannot_cammet {
  background-color: #687a86;
  font-size: 16px;
  color: #fff;
  padding: 5px 0;
  border-radius: 6px;
  margin-bottom: 20px;
}
.Header_drawer {
  font-size: 25px;
  color: #000;
  font-weight: 700;
  text-align: center;
  padding: 5px 0;
  border-bottom: 1px solid #ebeef2;
}
.ant-spin-spinning {
  display: flex;
  justify-content: center;
}
