@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&display=swap");
@import "../layout/variable";
* {
  box-sizing: border-box;
}

body {
  font-family: "Source Sans Pro", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  background-color: $primary-background;
  color: $dark-text;
}

.main__container {
  max-width: 1200px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

a {
  color: $link-color;
  text-decoration: none;
}
p {
  margin: 0;
  font-size: 14px;
  color: $dark-text;
}
.avatar {
  margin-right: 10px;
  max-width: 50px;
  width: 100%;
  height: 50px;
  .ant-avatar-square {
    border-radius: 4px;
    overflow: hidden;
    width: 50px;
    height: 50px;
    .anticon {
      width: 50px;
      height: 50px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: #c5c5c5;
    }
  }
}
.ant-drawer-body {
  padding: 0;
  overflow: hidden;
  .ant-tabs-nav {
    padding: 0 20px;
  }
}
